import React from "react";
import "./Myself.css";

function Myself() {
  return (
    <div className="img-container">
      <img src="img/egg.jpg" className="profile-img" alt=""></img>
      <div className="about-me">
        <h1 className="heading"> About me </h1>

        <p>
          I am Artur Meyster, a full-stack software engineer based in San
          Francisco. I fell in love with technology when I build my first
          website with HTML and CSS at the age of 12 and hosted it on Geocities!
          Since then I've been passionate about building applications that solve
          real world problems by applying new and exciting technologies. My
          latest experience has been focused on building single-page
          applications with JavaScript, Angular, Backbone, Ionic, HTML/CSS,
          jQuery, Node.js, MySQL and MongoDB.
        </p>
      </div>
    </div>
  );
}

export default Myself;
